import Vue from 'vue';
import VueRouter from 'vue-router';
Vue.use(VueRouter);

import {RxUtil} from '@/assets/util.js';
import {loadCustomList} from '@/assets/app.js';
import router from '@/router/index';

import RxBtnEdit from '@/components/common/RxBtnEdit.vue';
import {Input} from 'vue-ydui/dist/lib.rem/input';
import RxSearch from '@/components/common/RxSearch';
import NoData from '@/components/common/NoData';
import RxSearchPanel from '@/components/common/RxSearchPanel';
import rxRadio from '@/components/common/rxRadio';
import rxCheckbox from '@/components/common/rxCheckbox';
import RxCombo from '@/components/common/RxCombo';



Vue.component(RxSearch.name, RxSearch);
Vue.component(RxSearchPanel.name, RxSearchPanel);
Vue.component(NoData.name, NoData);
Vue.component(Input.name, Input);
Vue.component(rxRadio.name, rxRadio);
Vue.component(rxCheckbox.name, rxCheckbox);
Vue.component(RxCombo.name, RxCombo);
Vue.component(RxBtnEdit.name, RxBtnEdit);	

/**
 * 加载表单
 */
var loadingComponent = {
	  template: "<div>正在加载自定义列表</div>"
}

var listComponet= Vue.component('listComponet', {
  	template: '',
  	data(){
	  	return {
	  		page:0,
			total:0,
			list:[],
			selectIds:"",
			search:[],
			boData:{}
	  	};
	},
	methods:{
	  	showSenior(){
	   		this.$refs.searchPanel.show();
	   	},
	   	handSearch(){
	   		this.page=0;
	   		this.loadList(false);
	   	},
	   	toggle(item){
	   		item.expand=!item.expand;
	   	},
	   	loadList(merge){
	   		var listUrl= + this.boData.isShare=="YES"? "/dev/cus/customData/share/"+this.boData.key+"/getData.do" 
				: "/dev/cus/customData/"+this.boData.key +"/getData.do";
			listUrl=_baseUrl+listUrl;
				
			var searchParams="pageIndex="+this.page+"&pageSize=10";
			var tmp=RxUtil.serialJsonToForm(this.search);
			if(tmp){
				searchParams+="&" + tmp;
			}
			loadCustomList(listUrl,searchParams,this,data=>{
				this.total=data.total;
			},merge);
		},
		getStatusDisplay(status){
			if(!status) return "";
			var json={
				RUNNING:"运行中",DRAFTED:"草稿",SUCESS_END:"成功结束",
				DISCARD_END:"作废",ABORT_END:"异常中止结束",PENDING:"挂起"
			}
			return json[status];
		},
		formAdd(){
			router.push({name:"customForm",params:{boKey:this.boData.key,
				alias:this.boData.formAlias,id:"-1",readonly:false}})
		},
		formEdit(){
			if(!this.selectIds){
				this.errorToast("请选择要编辑的记录");
				return;
			}
			if(this.selectIds.indexOf(",")!=-1){
				this.errorToast("请选择记录一行");
				return;
			}
			router.push({name:"customForm",params:{boKey:this.boData.key,
				alias:this.boData.formAlias,id:this.selectIds,readonly:false}})
		},
		formDel(){
	   		var ids=this.selectIds;
	   		if(!ids){
				this.errorToast("请选择要删除的记录!");
	   			return;
	   		}
	   		this.$dialog.confirm({
    			title: '提示信息',
    			mes: '确定要删除选中的记录吗！',
    			opts: () => {
        			this.delById();	
    			}
			});
	   	},
	   	formDetail(){
	   		var ids=this.selectIds;
	   		if(!ids){
				this.errorToast("请选择要删除的记录!");
	   			return;
	   		}
	   		router.push({name:"customForm",params:{boKey:this.boData.key,
				alias:this.boData.formAlias,id:this.selectIds,readonly:true}})
	   	},
	   	
	   	delById(){
	   		var aryId=this.selectIds.split(",");
	   		var url=_baseUrl +"/sys/customform/sysCustomFormSetting/remove/"+this.alias +".do";
	   		var params="id=" + this.selectIds;
	   		this.$ajax.post(url,params).then(res=>{
	   			//删除 选择的记录显示。
	   			for(var i=0;i<aryId.length;i++){
		   			var id=aryId[i];
		   			for(var j=0;j<this.list.length;j++){
		   				var o=this.list[j];
		   				if(o[this.idField]==id){
		   					this.list.splice(j,1);
		   				}
		   			}
	   			}
	   		})
	   	}
	}
})

export default {
	name : "CustomList",
	data(){
	  	return {
	    	currentView: loadingComponent,
	    	alias:"",
	    	listInst:{}
	  	} 
	},
	created(){
		this.alias=this.$route.params.alias;
		var url=_baseUrl +"/sys/core/sysBoList/mobile/" + this.alias +".do";
		this.$dialog.loading.open('正在加载对话框...');
		this.$ajax.get(url).then(res => {
			this.$dialog.loading.close();
			var rtnData=res.data;
			var html=rtnData.mobileHtml;
			var htmlObj=RxUtil.parseHtml(html);
			listComponet.options.template=htmlObj.html;
			//插入脚本
			RxUtil.insertScript("listcontainer",htmlObj.script);
			//设置组件
			this.currentView=listComponet;
			
			this.$nextTick(function(){
				this.listInst=this.$refs.component;
				if("listComponet"==this.listInst.constructor.extendOptions.name){
					this.listInst.search=params_json.search;
					this.listInst.boData=rtnData;
					this.listInst.loadList(true);
				}
			});
		});			
	}
}




